<template>
  <!-- <h2>Add Resource</h2> -->
  <base-dialog
    v-if="inputIsInvalid"
    title="Invalid input"
    @close="confirmInput"
  >
    <template #default>
      <p>Unfortunately, at least one input value is invalid.</p>
      <p>Please check all the inputs and enter at least one character.</p>
    </template>
    <template #actions>
      <!-- <base-button @click="confirmInput">Okay</base-button> -->
    </template>
  </base-dialog>
  <base-card>
    <form>
      <div class="form-control">
        <label for="title">Title</label>
        <input type="text" name="title" id="title" ref="titleInput" />
      </div>
      <div class="form-control">
        <label for="desc">Description</label>
        <textarea
          name="description"
          id="description"
          rows="3"
          ref="descInput"
        ></textarea>
      </div>
      <div class="form-control">
        <label for="link">Link</label>
        <input type="url" name="link" id="link" ref="urlInput" />
      </div>
      <div class="form-control">
        <base-button type="submit" @click.prevent="submitData"
          >Add Resource</base-button
        >
      </div>
    </form>
  </base-card>
</template>

<script>
import BaseButton from '../UI/BaseButton.vue';
export default {
  components: { BaseButton },
  data() {
    return {
      inputIsInvalid: false,
    };
  },
  inject: ['addNewResource'],
  methods: {
    submitData() {
      const enteredTitle = this.$refs.titleInput.value;
      const enteredDesc = this.$refs.descInput.value;
      const enteredLink = this.$refs.urlInput.value;

      if (
        enteredTitle.trim() === '' ||
        enteredDesc.trim() === '' ||
        enteredLink.trim() === ''
      ) {
        this.inputIsInvalid = true;
        return;
      }

      this.addNewResource(enteredTitle, enteredDesc, enteredLink);

      this.$refs.titleInput.value = '';
      this.$refs.descInput.value = '';
      this.$refs.urlInput.value = '';
    },
    confirmInput() {
      this.inputIsInvalid = false;
    },
  },
};
</script>

<style scoped>
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.15rem;
  border: 1px solid #ccc;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #3a0061;
  background-color: #f7ebff;
}

.form-control {
  margin: 1rem 0;
}
</style>
